import React, { useState, useEffect, useCallback } from 'react';
import { Modal, Button, Input, Upload, message, Drawer, Form, Space, Spin, Card, Row, Col } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined } from '@ant-design/icons';
import { db, storage } from '../../config';
import { collection, addDoc, getDocs, doc, updateDoc } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';

const { TextArea } = Input;

const EditFrontend = () => {
    const [form] = Form.useForm();
    const [formValues, setFormValues] = useState({
        title: '',
        content: '',
        logo: { url: '', title: '' },
        coverImage1: '',
        coverImage2: '',
        footerImages: [],
        icons: [],
        addresses: []
    });

    const [loading, setLoading] = useState(true);
    const [documentId, setDocumentId] = useState(null);

    const [iconModalVisible, setIconModalVisible] = useState(false);
    const [addressModalVisible, setAddressModalVisible] = useState(false);
    const [drawerVisible, setDrawerVisible] = useState(false);
    const [editingItem, setEditingItem] = useState(null);
    const [editingType, setEditingType] = useState('');

    const [tempLogoImage, setTempLogoImage] = useState('');
    const [tempLogoTitle, setTempLogoTitle] = useState('');

    const [tempIcons, setTempIcons] = useState([]);
    const [tempAddresses, setTempAddresses] = useState([]);

    const fetchExistingData = useCallback(async () => {
        try {
            const querySnapshot = await getDocs(collection(db, 'Frontend'));
            if (!querySnapshot.empty) {
                const doc = querySnapshot.docs[0];
                const data = doc.data();
                setFormValues(data);
                setDocumentId(doc.id);
                form.setFieldsValue({
                    title: data.title,
                    content: data.content
                });
            }
        } catch (error) {
            console.error("Error fetching data: ", error);
        } finally {
            setLoading(false);
        }
    }, [form]);

    useEffect(() => {
        fetchExistingData();
    }, [fetchExistingData]);

    const handleImageUpload = async (file) => {
        const storageRef = ref(storage, `CAREERS/CUSTOMS/${file.name}_${Date.now()}`);
        try {
            const snapshot = await uploadBytes(storageRef, file);
            const downloadURL = await getDownloadURL(snapshot.ref);
            return downloadURL;
        } catch (error) {
            console.error("Error uploading image: ", error);
        }
    };

    const handleLogoImageSubmit = async () => {
        if (tempLogoImage) {
            setFormValues(prev => ({ ...prev, logo: { ...prev.logo, url: tempLogoImage } }));
            setTempLogoImage('');
        }
    };

    const handleLogoTitleSubmit = async () => {
        if (tempLogoTitle) {
            setFormValues(prev => ({ ...prev, logo: { ...prev.logo, title: tempLogoTitle } }));
            setTempLogoTitle('');
        }
    };

    const handleLogoImageDelete = () => {
        setFormValues(prev => ({ ...prev, logo: { ...prev.logo, url: '' } }));
        message.success('Logo image deleted successfully');
    };

    const handleLogoTitleDelete = () => {
        setFormValues(prev => ({ ...prev, logo: { ...prev.logo, title: '' } }));
        message.success('Logo title deleted successfully');
    };

    const handleAddressSubmit = async () => {
        if (tempAddresses.length > 0) {
            setFormValues(prev => ({ ...prev, addresses: [...prev.addresses, ...tempAddresses] }));
            setAddressModalVisible(false);
            setTempAddresses([]);
        }
    };

    const handleSubmit = async () => {
        try {
            const values = await form.validateFields();
            const finalValues = { ...formValues, ...values };
            if (documentId) {
                await updateDoc(doc(db, 'Frontend', documentId), finalValues);
                message.success('Cập nhật thành công');
            } else {
                const docRef = await addDoc(collection(db, 'Frontend'), finalValues);
                setDocumentId(docRef.id);
                message.success('Form submitted successfully');
            }
        } catch (error) {
            console.error("Error updating/adding document: ", error);
            message.error('Failed to submit form');
        }
    };

    const showDrawer = (item, type) => {
        setEditingItem(item);
        setEditingType(type);
        setDrawerVisible(true);
    };

    const handleDrawerClose = () => {
        setDrawerVisible(false);
        setEditingItem(null);
        setEditingType('');
    };

    const handleDrawerSubmit = async () => {
        try {
            const values = await form.validateFields();
            const file = form.getFieldValue('file');
            let imageUrl = editingItem.url;

            if (file && file[0]) {
                imageUrl = await handleImageUpload(file[0].originFileObj);
            }

            const updatedItem = { ...values, url: imageUrl };

            if (editingType === 'icon') {
                setFormValues(prev => ({
                    ...prev,
                    icons: prev.icons.map(icon =>
                        icon === editingItem ? updatedItem : icon
                    )
                }));
            } else if (editingType === 'address') {
                setFormValues(prev => ({
                    ...prev,
                    addresses: prev.addresses.map(address =>
                        address === editingItem ? updatedItem : address
                    )
                }));
            }
            handleDrawerClose();
            message.success('Item updated successfully');
        } catch (error) {
            console.error("Error updating item: ", error);
            message.error('Failed to update item');
        }
    };

    const handleIconSubmit = async () => {
        if (tempIcons.length > 0) {
            setFormValues(prev => ({ ...prev, icons: [...prev.icons, ...tempIcons] }));
            setIconModalVisible(false);
            setTempIcons([]);
        }
    };

    const handleDelete = (index, type) => {
        if (type === 'icon') {
            setFormValues(prev => ({
                ...prev,
                icons: prev.icons.filter((_, i) => i !== index)
            }));
        } else if (type === 'address') {
            setFormValues(prev => ({
                ...prev,
                addresses: prev.addresses.filter((_, i) => i !== index)
            }));
        }
        message.success('Item deleted successfully');
    };

    if (loading) {
        return <Spin size="large" />;
    }

    return (
        <div>
            <Form form={form} onFinish={handleSubmit} layout="vertical">
                <Row gutter={[16, 16]}>
                    <Col xs={24} sm={12} md={8} lg={6}>
                        <Card title="Logo Image" extra={<Button icon={<PlusOutlined />}>
                            {formValues.logo.url ? 'Sửa logo' : 'Thêm logo'}
                        </Button>}>
                            {formValues.logo.url && (
                                <div>
                                    <img src={formValues.logo.url} alt="Logo" style={{ width: '100%', marginBottom: 10 }} />
                                    <Button
                                        icon={<DeleteOutlined />}
                                        danger
                                        onClick={handleLogoImageDelete}
                                        block
                                    >
                                        Xóa logo
                                    </Button>
                                </div>
                            )}
                            <Upload
                                beforeUpload={async (file) => {
                                    const url = await handleImageUpload(file);
                                    setTempLogoImage(url);
                                    return false;
                                }}
                            >
                                <Button icon={<PlusOutlined />} onClick={handleLogoImageSubmit} block>Cập nhật logo</Button>
                            </Upload>
                        </Card>
                    </Col>
                    <Col xs={24} sm={12} md={8} lg={6}>
                        <Card title="Logo Title" extra={<Button icon={<EditOutlined />}>
                            {formValues.logo.title ? 'Sửa tiêu đề logo' : 'Thêm tiêu đề logo'}
                        </Button>}>
                            {formValues.logo.title ? (
                                <div>
                                    <p>{formValues.logo.title}</p>
                                    <Button
                                        icon={<DeleteOutlined />}
                                        danger
                                        onClick={handleLogoTitleDelete}
                                        block
                                    >
                                        Xóa tiêu đề logo
                                    </Button>
                                </div>
                            ) : (
                                <p>Chưa có tiêu đề logo</p>
                            )}
                            <Input
                                placeholder="Nhập tiêu đề logo"
                                value={tempLogoTitle}
                                onChange={(e) => setTempLogoTitle(e.target.value)}
                                style={{ marginBottom: 10 }}
                            />
                            <Button onClick={handleLogoTitleSubmit} block>Cập nhật tiêu đề</Button>
                        </Card>
                    </Col>
                </Row>

                <Form.Item name="title" label="Title" rules={[{ required: true }]}>
                    <Input />
                </Form.Item>
                <Form.Item name="content" label="Content" rules={[{ required: true }]}>
                    <TextArea rows={4} />
                </Form.Item>
                <div className="mb-3">
                    <Button onClick={() => setIconModalVisible(true)}>Thêm icon</Button>
                    <div className="d-flex flex-wrap mt-2">
                        {formValues.icons.map((icon, index) => (
                            <div key={index} className="me-2 mb-2">
                                <img src={icon.url} alt={icon.title} style={{ width: 50 }} />
                                <p>{icon.title}</p>
                                <Space>
                                    <Button icon={<EditOutlined />} onClick={() => showDrawer(icon, 'icon')} />
                                    <Button icon={<DeleteOutlined />} onClick={() => handleDelete(index, 'icon')} danger />
                                </Space>
                            </div>
                        ))}
                    </div>
                </div>

                <div className="mb-3">
                    <Button onClick={() => setAddressModalVisible(true)}>Thêm địa chỉ</Button>
                    <div className="mt-2">
                        {formValues.addresses.map((address, index) => (
                            <div key={index} className="mb-2">
                                <img src={address.image} alt="Address" style={{ width: 100 }} />
                                <p>{address.content}</p>
                                <Space>
                                    <Button icon={<EditOutlined />} onClick={() => showDrawer(address, 'address')} />
                                    <Button icon={<DeleteOutlined />} onClick={() => handleDelete(index, 'address')} danger />
                                </Space>
                            </div>
                        ))}
                    </div>
                </div>

                <Form.Item>
                    <Button type="primary" htmlType="submit">
                        {documentId ? 'Cập nhật' : 'Gửi'}
                    </Button>
                </Form.Item>
            </Form>

            <Modal
                title="Thêm icon"
                visible={iconModalVisible}
                onOk={handleIconSubmit}
                onCancel={() => setIconModalVisible(false)}
            >
                {tempIcons.map((icon, index) => (
                    <div key={index} style={{ marginBottom: 10 }}>
                        <img src={icon.url} alt={icon.title} style={{ width: 50, marginRight: 10 }} />
                        <Input
                            placeholder="Tiêu đề icon"
                            value={icon.title}
                            onChange={(e) => {
                                const newIcons = [...tempIcons];
                                newIcons[index].title = e.target.value;
                                setTempIcons(newIcons);
                            }}
                            style={{ width: 200 }}
                        />
                        <Button icon={<DeleteOutlined />} onClick={() => setTempIcons(icons => icons.filter((_, i) => i !== index))} danger />
                    </div>
                ))}
                <Upload
                    beforeUpload={async (file) => {
                        const url = await handleImageUpload(file);
                        setTempIcons(prev => [...prev, { url, title: '' }]);
                        return false;
                    }}
                >
                    <Button icon={<PlusOutlined />}>Thêm icon</Button>
                </Upload>
            </Modal>

            <Modal
                title="Thêm địa chỉ"
                visible={addressModalVisible}
                onOk={handleAddressSubmit}
                onCancel={() => setAddressModalVisible(false)}
            >
                {tempAddresses.map((address, index) => (
                    <div key={index} style={{ marginBottom: 10 }}>
                        <img src={address.image} alt="Address" style={{ width: 100, marginRight: 10 }} />
                        <TextArea
                            placeholder="Nhập địa chỉ"
                            value={address.content}
                            onChange={(e) => {
                                const newAddresses = [...tempAddresses];
                                newAddresses[index].content = e.target.value;
                                setTempAddresses(newAddresses);
                            }}
                            style={{ width: 200 }}
                        />
                        <Button icon={<DeleteOutlined />} onClick={() => setTempAddresses(addresses => addresses.filter((_, i) => i !== index))} danger />
                    </div>
                ))}
                <Upload
                    beforeUpload={async (file) => {
                        const image = await handleImageUpload(file);
                        setTempAddresses(prev => [...prev, { image, content: '' }]);
                        return false;
                    }}
                >
                    <Button icon={<PlusOutlined />}>Thêm địa chỉ</Button>
                </Upload>
            </Modal>

            <Drawer
                title={`Chỉnh sửa ${editingType === 'icon' ? 'Icon' : 'Địa chỉ'}`}
                placement="right"
                closable={false}
                onClose={handleDrawerClose}
                visible={drawerVisible}
            >
                {editingItem && (
                    <Form form={form} onFinish={handleDrawerSubmit} layout="vertical" initialValues={editingItem}>
                        <Form.Item name="title" label="Tiêu đề" rules={[{ required: true }]}>
                            <Input />
                        </Form.Item>
                        <Form.Item name="file" label="Hình ảnh" valuePropName="fileList" getValueFromEvent={(e) => e && e.fileList}>
                            <Upload
                                beforeUpload={() => false}
                                listType="picture-card"
                                maxCount={1}
                            >
                                <div>
                                    <PlusOutlined />
                                    <div style={{ marginTop: 8 }}>Tải lên</div>
                                </div>
                            </Upload>
                        </Form.Item>
                        {editingType === 'address' && (
                            <Form.Item name="content" label="Nội dung" rules={[{ required: true }]}>
                                <TextArea rows={4} />
                            </Form.Item>
                        )}
                        <Form.Item>
                            <Button type="primary" htmlType="submit">
                                Cập nhật
                            </Button>
                        </Form.Item>
                    </Form>
                )}
            </Drawer>
        </div>
    );
};

export default EditFrontend;